.item_wrapper {
    display: flex;
    flex-direction: column;
    width: 50%;
    align-items: center;
}

.item_wrapper img {
    width: 80%;
}

.item_info div {
    margin: 0.3vh 0 0.3vh 0;
}

.item_title {
    font-size: small;
    color: #FFFFFF;
}

.item_color {
    color: #AD9999;
    font-size: x-small;
}

.item_price {
    color: #FFFFFF;
    font-weight: 100;
}